<template>
  <div class="home">
    <ul>
      <li v-for="item in data" :key="item.title">
        <p class="title">{{item.title}}</p>
        <div
          class="component"
          v-for="component in item.list"
          :key="component.name"
          @click="selFun(component.name)"
        >{{component.name}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [
        {
          title: 'JS components', list: [
            { name: 'Toast' },
            { name: 'Loading' }
          ]
        },
        {
          title: 'CSS components', list: [
            { name: 'Header' },
            { name: 'Button' }
          ]
        }
      ]
    }
  },
  methods: {
    selFun(name) {
      this.$router.push({ name: name })
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  ul {
    li {
      cursor: pointer;
      .title {
        line-height: 50px;
        text-align: center;
        background-color: #f2f2f2;
      }
      .component {
        line-height: 30px;
        padding: 10px;
        border-bottom: 1px solid #f2f2f2;
      }
    }
  }
}
</style>

